body {
  background-color: #161623;
  font-family: "Poppins", sans-serif;
}

body::before {
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  background: linear-gradient(#f00, #f0f);
  clip-path: circle(30% at 80% 52%);
}

body::after {
  content: "";
  position: absolute;
  height: 100%;
  top: 0;
  left: 0;
  width: 100%;
  background: linear-gradient(
    rgb(5, 182, 226),
    rgb(240, 48, 14),
    rgb(238, 75, 46)
  );
  clip-path: circle(25% at 10% 25%);
}

/*......................................Nav bar Design................................... */

.Container-1 {
  position: fixed;
  width: calc(100% - 40px);
  height: 0px;
  z-index: 3;
  background: rgba(255, 255, 255, 0.1);
  box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.1);
  border-radius: 20px;
  justify-content: space-between;
  overflow: hidden;
  align-items: center;
  display: flex;
  padding: 30px;
  backdrop-filter: blur(20px);
  margin: 2%;
  transition: 0.5s ease all;
}

.Container-1 ul {
  display: flex;
}

.Container-1 .logo {
  font-size: 1.5em;
  font-weight: 900;
  color: rgb(68, 3, 3);
  cursor: pointer;
}

.Container-1 .logo:hover {
  background: rgba(255, 255, 255, 0.1);
  box-shadow: 5px 5px 5px rgba(243, 247, 241, 0.5);
  padding: 9px;
  border-radius: 80px;
  backdrop-filter: blur(30px);
  text-transform: uppercase;
}

.Container-1 ul li {
  list-style: none;
  margin: 0 20px;
}

.Container-1 ul li .nav-bar-link {
  text-decoration: none;
  color: white;
  font-size: 1.5em;
  text-transform: capitalize;
}

.nav-bar-link:hover {
  background: rgba(255, 255, 255, 0.1);
  box-shadow: 5px 5px 5px rgba(243, 247, 241, 0.5);
  padding: 7px;
  border-radius: 20px;
  backdrop-filter: blur(30px);
  text-transform: uppercase;
  font-weight: 600;
}


@media screen and (max-width: 480px) {
  .Container-1 ul li {
    margin: 0 15px;
  }
  .Container-1 .logo {
    font-size: 1.2em;
  }
  .Container-1 .logo:hover {
    padding: 3px;
    border-radius: 10px;
  }

  .Container-1 ul li .nav-bar-link {
    font-size: 0.8em;
    letter-spacing: 0.7px;
  }
  
  .Container-1 {
    flex-direction: column;  
    padding:100px;
    padding-top: 5px;
    margin: 4%;
    padding-bottom: 70px;
    margin-top: 3%;
  }
}
@media screen and (max-width: 350px) {
  body{
    background-color: black;
  }
  .Container-1 .logo {
    font-size: 1.2em;
  }
  .Container-1 .logo:hover {
    padding: 3px;
    border-radius: 10px;
  }
  .Container-1 ul li {
    margin: 0 5px;
  }

  .Container-1 ul li .nav-bar-link {
    font-size: 0.8em;
    letter-spacing: 0.3px;
  }
  
.Container-1 {
  flex-direction: column;  
  padding:100px;
  padding-top: 5px;
  margin: 4%;
  padding-bottom: 70px;
  margin-top: 3%;
}
}